<script>
import { Message, InfoFilled, Coffee } from '@element-plus/icons-vue'
import { navData } from "../nav-data.js"

export default {
  name: 'NavigationFooter',
  components: {
    Message,
    InfoFilled,
    Coffee
  },
  props: {
    currentTheme: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      version: '1.0.0',

    }
  },
  computed: {
    totalCategories() {
      return this.getUniqueCategories().length
    },
    totalLinks() {
      return navData.length
    },
    lastUpdateTime() {
      return new Date().toLocaleDateString('zh-CN', {
        year: 'numeric',
        month: 'long',
        day: 'numeric'
      })
    }
  },
  methods: {
    getUniqueCategories() {
      const categories = new Set()
      navData.forEach(item => {
        if (item.category) {
          categories.add(item.category)
        }
      })
      return Array.from(categories)
    }
  }
}
