<!-- 导航栏头部组件 -->
<template>
  


  <!-- 顶部菜单栏，支持暗黑模式切换 -->
  <el-menu
    :default-active="activeIndex"
    class="el-menu-demo"
    :class="{ 'dark-theme': currentTheme === 'dark' }"
    mode="horizontal">


    <!-- 首页菜单项 -->
    <el-menu-item index="home" @click="handleHome" class="home-menu-item">
      <el-icon><House /></el-icon>主页
    </el-menu-item>
    <!-- 关于页面菜单项 -->
    <el-menu-item index="about" @click="$emit('show-about')" v-show="true">
      <el-icon><InfoFilled /></el-icon>关于
    </el-menu-item>
    
    <!-- 捐赠页面菜单项（仅图标） -->
    <el-menu-item index="donate" @click="$emit('show-donate')" title="捐赠">
      <el-icon><Coffee /></el-icon>捐赠
    </el-menu-item>

    <!-- 提交网站菜单项（当前隐藏） -->
    <el-menu-item index="reward" @click="$emit('show-reward')" v-show="false">
      <el-icon><Plus /></el-icon>提交网站
    </el-menu-item>

    <el-menu-item index="theme" @click="$emit('toggle-theme')">
      <el-icon>
        <Sunny v-if="currentTheme === 'dark'" />
        <Moon v-else />
      </el-icon>暗黑模式
    </el-menu-item>

    <!-- 搜索项，位于菜单中，样式根据主题动态调整 -->
    <el-menu-item
    index="search"
    class="search-item"
    style="margin-left: auto; position: relative; z-index: 3000;"
    @click="handleMenuClick"
    @mousedown="handleMenuMouseDown"
    @touchstart="handleMenuTouchStart"
  >
      <div class="search-container" @click.stop @mousedown.stop @touchstart.stop>
        <!-- 搜索引擎选择下拉框，根据当前主题调整样式 -->
        <el-select
          v-model="selectedEngine"
          class="search-engine-select"
          :teleported="false"
          :style="[
            
            { width: '300px' }
          ]"
        >
          <template #prefix>
            <el-icon class="search-engine-icon">
              <component :is="currentEngineIcon" />
            </el-icon>
          </template>
          <el-option
            v-for="engine in searchEngines"
            :key="engine.value"
            :label="engine.label"
            :value="engine.value"
            :class="{ 'dark-theme': currentTheme === 'dark' }"
            class="search-engine-option"
          >
            <div class="search-engine-option-content">
              <el-icon class="search-engine-icon">
                <component :is="engine.icon" />
              </el-icon>
              <span>{{ engine.label }}</span>
            </div>
          </el-option>
        </el-select>
        <!-- 搜索输入框，带有搜索按钮，根据当前主题调整样式 -->
        <el-input
          ref="searchInput"
          v-model="searchText"
          @keyup.enter="handleSearch"
          @input="handleSearchInput"
          class="search-input"
          @keydown="handleKeyDown"
          placeholder="输入关键词搜索"
        >
        

          <template #append>
            <el-button @click="handleSearch">
              <el-icon><Search /></el-icon>
            </el-button>
            <!-- 清空按钮 -->
            <el-button 
              @click="clearSearch" 
              :class="{ 'dark-theme': currentTheme === 'dark' }"
              v-if="searchText"
              class="clear-button"
            >
              <el-icon><Close /></el-icon>
            </el-button>
          </template>
        </el-input>

      </div>
    </el-menu-item>
  </el-menu>
</template>

<script>
// 导入 Element Plus 图标组件
import { House, InfoFilled, Coffee, Plus, Moon, Sunny, Search, Close, ChromeFilled, Picture, VideoPlay, Menu } from '@element-plus/icons-vue'
import { getPinyinInitials } from "../utils/pinyin.js"

export default {
  name: 'NavigationHeader',
  // 注册使用的图标组件
  components: {
    House,
    InfoFilled,
    Coffee,
    Plus,
    Moon,
    Sunny,
    Search,
    Close,
    ChromeFilled,
    Picture,
    VideoPlay,
    Menu // 添加折叠按钮的图标组件
  },
  // 组件属性定义
  props: {
    // 当前激活的菜单项索引
    activeIndex: {
      type: String,
      default: ''
    },
    // 当前主题（light/dark）
    currentTheme: {
      type: String,
      required: true
    }
  },
  // 定义组件可触发的事件
  emits: ['show-about', 'show-donate', 'show-reward', 'select', 'toggle-theme', 'test', 'search-input'],
  data() {
    return {
      categories: [],             // ✅ 这一行必须有！
      filteredCategories: [],     // ✅ 这一行也要有
      searchText: '',
      selectedEngine: 'google',
      searchEngines: [
        {
          label: 'Google',
          value: 'google',
          url: 'https://www.google.com/search?q=%s',
          icon: 'ChromeFilled'
        },
        {
          label: 'Bing',
          value: 'bing',
          url: 'https://www.bing.com/search?q=%s',
          icon: 'Search' // 更换为Search图标，更符合搜索引擎特性
        },
        {
          label: '百度',
          value: 'baidu',
          url: 'https://www.baidu.com/s?wd=%s',
          icon: 'Search'
        },
        {
          label: '抖音搜索',
          value: 'douyin',
          url: 'https://www.douyin.com/search/%s',
          icon: 'Search'
        },
         {
          label: '快手搜索',
          value: 'kuaishou',
          url: 'https://www.kuaishou.com/search/%s',
          icon: 'Search'
        },
        {
          label: 'B站搜索',
          value: 'bilibili',
          url: 'https://www.bilibili.com/search?keyword=%s',
          icon: 'Search'
        },
        {
          label: 'Youtube搜索',
          value: 'youtube',
          url: 'https://www.youtube.com/results?search_query=%s',
          icon: 'Search'
        },
        {
          label: 'Google Images',
          value: 'google_images',
          url: 'https://www.google.com/search?tbm=isch&q=%s',
          icon: 'Picture'
        },
        {
          label: 'Google Videos',
          value: 'google_videos',
          url: 'https://www.google.com/search?tbm=vid&q=%s',
          icon: 'VideoPlay'
        },

        {
          label: 'Google Scholar',
          value: 'scholar',
          url: 'https://scholar.google.com/scholar?q=%s',
          icon: 'Search'
        },
        {
          label: 'GitHub',
          value: 'github',
          url: 'https://github.com/search?q=%s',
          icon: 'Search'
        },
        {
          label: 'Reddit搜索',
          value: 'reddit',
          url: 'https://www.reddit.com/search/?q=%s',
          icon: 'Search'
        },
        {
          label: 'Stack Overflow',
          value: 'stackoverflow',
          url: 'https://stackoverflow.com/search?q=%s',
          icon: 'Search'
        },

        {
          label: 'Medium',
          value: 'medium',
          url: 'https://medium.com/search?q=%s',
          icon: 'Search'
        },

      ],
      isMobile: false, // 用于判断是否为移动端
    }
  },
  //
  computed: {
    currentEngineIcon() {
      return this.searchEngines.find(engine => engine.value === this.selectedEngine)?.icon || 'Search'
    }
  },
  mounted() {
    this.checkIfMobile();
    window.addEventListener('resize', this.checkIfMobile);
    window.addEventListener('keydown', this.handleKeydown); // ✅ 监听键盘
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.checkIfMobile);
    window.removeEventListener('keydown', this.handleKeydown); // ✅ 清除监听
  },
  methods: {
    checkIfMobile() {
      this.isMobile = window.innerWidth <= 868; // 根据屏幕宽度判断
    },

    handleKeydown(event) {
    // 限定只处理输入框聚焦时的空格
    const input = this.$refs.searchInput?.input;
    if (!input || document.activeElement !== input) return;
     // 如果输入法正在激活，不处理
    if (event.isComposing) return;

    // 空格键的 key 是 ' '，code 是 'Space'
    if (event.code === 'Space' || event.key === ' ') {
      event.preventDefault(); // 阻止默认行为（防止页面滚动）
      this.handleSpace();     // 调用你已经写好的空格处理逻辑
    }
  },

  // 搜索分类
    filterCategories() {
    if (!this.searchText) {
      this.filteredCategories = this.categories;
      return;
    }
    const searchLower = this.searchText.toLowerCase();
    const pinyinInitials = getPinyinInitials(this.searchText).toLowerCase();

    this.filteredCategories = this.categories.filter(category => {
      const categoryName = category.name.toLowerCase();
      const catPinyin = getPinyinInitials(category.name).toLowerCase();
      return (
        categoryName.includes(searchLower) || catPinyin.includes(pinyinInitials)
      );
    });
  },

    // 处理菜单项选择事件
    handleSelect(key) {
      this.$emit('select', key)
    },
    // 处理首页跳转,点击首页跳转到
    handleHome() {
      window.location.href = 'https://axaaaaa.github.io/';
    },
    // 处理空格键输入
    handleSpace() {
      this.searchText += ' ';
      this.$emit('search-input', this.searchText);
    },
    // 处理搜索
    // 安全的菜单点击处理，避免e.stopPropagation is not a function错误
    handleMenuClick(e) {
      if (e && typeof e.stopPropagation === 'function') {
        e.stopPropagation();
      }
    },
    
    handleSearch(event) {
      if (event && typeof event.stopPropagation === 'function') {
        event.stopPropagation();
      }
      if (!this.searchText) return;
      const searchUrl = this.searchEngines.find(engine => engine.value === this.selectedEngine)?.url;
      if (searchUrl) {
        window.open(searchUrl.replace('%s', encodeURIComponent(this.searchText)), '_blank');
      }
    },
    handleSearchInput(value) {
      this.$emit('search-input', value);
      this.filterCategories(); // 👈 实时过滤
      const query = value.trim().toLowerCase();
      if (!query) {
        this.filteredSuggestions = [];
        return;
      }
      



    },
    clearSearch() {
      this.searchText = '';
      this.$emit('search-input', '');  // 触发搜索输入变化事件
    }
  }
}
</script>

