<template>
  <!-- 导航栏，根据当前主题动态添加暗黑主题类 -->
  <div class="navigation" :class="{ 'dark-theme': currentTheme === 'dark' }">
    
    <!-- 搜索框 + 搜索选项 -->
    <div class="search-container">
      <div class="search-bar">
        <el-input
          v-model="localSearchQuery"
          :placeholder="currentPlaceholder"
          clearable
          class="search-input"
          @input="updateSuggestions"         
          @keydown.enter="performSearch"
        >
          <template #prepend>
            <el-select v-model="searchMode" class="search-mode" style="width: 100px">
              <el-option label="Google" value="Google"></el-option>
              <el-option label="Bing" value="Bing"></el-option>
              <el-option label="Sogou" value="Sogou"></el-option>
              <el-option label="Baidu" value="Baidu"></el-option>
            </el-select>
          </template>
        </el-input>

        <el-button class="search-button" type="default" @click="performSearch">搜索</el-button>

      </div>
      <div v-if="filteredSuggestions.length" class="suggestions">
        <ul>
          <li v-for="suggestion in filteredSuggestions" :key="suggestion.name">
            <el-button @click="selectSuggestion(suggestion)">{{ suggestion.name }}</el-button>
          </li>
        </ul>
      </div>
    </div>

    <!-- 顶部分页器 -->
    <div class="pagination-container pagination-top">
      <el-pagination
        v-model:current-page="currentPage"
        :page-size="pageSize"
        :total="filteredCategories.length"
        layout="prev, pager, next"
        @current-change="handlePageChange"
        background
      />
    </div>
   <!--雪地背景图-->
    <!-- 背景图片放在分类容器外面 - 包含背景图和叠加图 -->
    <div v-if="paginatedCategories.length > 0" class="first-category-background" :class="{ 'dark-theme': currentTheme === 'dark' }">
      <!-- 基础背景图 -->
      <img 
        src="@/assets/bgz.png" 
        alt="Background Image" 
        class="first-category-image"
      />
      <!-- 叠加图片 - bot1.png -->
      <img 
        src="@/assets/bot1.png" 
        alt="Overlay Image" 
        class="overlay-image"
      />
      <!-- 叠加图片 - bot2.png -->
      <img 
        src="@/assets/bot2.png" 
        alt="Overlay Image 2" 
        class="overlay-image-right"
      />
    </div>

    <!-- 循环显示分类及其对应的链接 -->
    <div
      v-for="category in paginatedCategories"
      :key="category.name"
      class="category"
      :data-category="category.name"
    >
      
      <!-- 分类标题-->
      <h2 class="category-title">
        <!-- 显示分类图标 -->
        <el-icon style="display: inline-flex; margin-right: 4px;">
          <component :is="category.icon" />
        </el-icon>
        {{ category.name }}
        <span class="category-count">{{ category.links.length }}</span> <!-- 分类链接数量 -->
      </h2>

      <!-- 当分类未收起时显示链接列表 -->
      <div class="links-container">
        <ul>
          <li v-for="link in category.links" :key="link.name">
            <!-- 链接按钮，带有工具提示显示描述 -->
            <el-tooltip class="item" effect="dark" :content="link.description" placement="top">
              <a :href="link.url" target="_blank"> 
                <el-button :type="currentTheme === 'dark' ? 'primary' : 'plain'" class="link-button">
                  
                  <!-- 显示网站favicon --> <!-- 如果链接的favicon URL存在 -->  <!-- 设置favicon的源为链接的favicon URL --> <!-- 设置alt属性为链接的名称 -->
                  <img  
                    v-if="getFaviconUrl(link.url)"  
                    :src="getFaviconUrl(link.url)" 
                    :alt="link.name"  
                    class="favicon"  
                    @error="handleImageError($event, link.url)" 
                  /> <!-- 处理图片加载错误，调用handleImageError方法 --><!-- 应用favicon的CSS类 -->
                  <span class="link-text">{{ link.name }}</span>
                  <template v-if="link.tag || (link.tags && link.tags.length)">
                    <el-tag
                      v-for="(t, idx) in normalizeTags(link)"
                      :key="idx + '-' + t"
                      size="small"
                      :type="getTagType(t)"
                      class="link-mini-tag"
                      effect="plain"
                    >
                      {{ t }}
                    </el-tag>
                  </template>
                </el-button>
              </a>
            </el-tooltip>
          </li>
        </ul>
      </div>
    </div>


    <!-- 分页器 -->
    <div class="pagination-container">
      <el-pagination
        v-model:current-page="currentPage"
        :page-size="pageSize"
        :total="filteredCategories.length"
        layout="prev, pager, next"
        @current-change="handlePageChange"
        background
      />
    </div>
  </div>
</template>

<script>
export default {
  name: 'NavigationLinks',
  // 组件属性定义
  props: {
    categories: {
      type: Array,
      required: true    // 导航分类数据，必填
    },
    currentTheme: {
      type: String,
      required: true    // 当前主题，必填
    },
    searchQuery: {
      type: String,
      default: ''      // 搜索关键词，可选
    },
    collapsedCategories: {
      type: Array,
      default: () => [] // 已折叠的分类列表，默认为空数组
    },
    targetCategory: {
      type: String,
      default: ''
    },
    watch: {
      targetCategory: {
        handler(newValue) {
          this.scrollToCategory(newValue);
        },
        immediate: true
      },
      localSearchQuery() {
        this.currentPage = 1;
      }
    },
  },
  data() {
    return {
      currentPage: 1,
      pageSize: 15,
      localSearchQuery: '', // 搜索框的内容
      searchMode: 'Google', // 默认搜索模式
      currentPlaceholderIndex: 0,
     
      filteredSuggestions: [], // 用于存储过滤后的建议
    };
  },
  mounted() {
  setInterval(() => {
    this.currentPlaceholderIndex =
      (this.currentPlaceholderIndex + 1) % this.searchSuggestions.length;
  }, 3000);
},
  computed: {
    currentPlaceholder() {
      return this.searchSuggestions[this.currentPlaceholderIndex] || '请输入关键词搜索';
    },
  // 根据搜索关键词过滤分类和链接
  filteredCategories() {
    const query = this.localSearchQuery.trim().toLowerCase();

    // ✅ 如果搜索框为空，直接显示所有分类
    if (!query) return this.categories;

    // ✅ 判断是否输入网址（如 https:// 或 www. 或 .com）
    const isUrl = query.startsWith('http') || query.startsWith('www.') || query.includes('.');

    return this.categories.map(category => {
      const filteredLinks = category.links.filter(link => {
        // ✅ 如果是网址，就匹配 link.url
        if (isUrl) {
          return link.url.toLowerCase().includes(query);
        }
        // ✅ 否则匹配名称或描述
        return (
          link.name.toLowerCase().includes(query) ||
          (link.description && link.description.toLowerCase().includes(query)) ||
          link.url.toLowerCase().includes(query)
        );
      });

      // ✅ 只保留匹配到的分类
      if (filteredLinks.length > 0) {
        return { ...category, links: filteredLinks };
      }
      return null;
    }).filter(category => category !== null);
  },

  searchSuggestions(){
    // 从分类中提取关键词（网站名、描述、标签）
    const words = new Set();
    this.categories.forEach(cat => {
      cat.links.forEach(link => {
        if (link.name) words.add(link.name);
        if (link.description) {
          // 拆分描述中的关键词（可选）
          link.description.split(/[ ,，。]/).forEach(w => {
            if (w.length >= 2 && w.length <= 8) words.add(w.trim());
          });
        }
        if (link.tags) {
          link.tags.forEach(tag => words.add(tag));
        }
      });
    });
    // 转成数组并随机取前10个
    return Array.from(words).sort(() => Math.random() - 0.5).slice(0, 10);
  },
  



    paginatedCategories() {
      const start = (this.currentPage - 1) * this.pageSize;
      const end = start + this.pageSize;
      return this.filteredCategories.slice(start, end);
    }
  },
  methods: {
    // 规范化标签：支持 link.tag 或 link.tags
    normalizeTags(link) {
      if (Array.isArray(link.tags)) return link.tags;
      if (typeof link.tag === 'string' && link.tag.trim()) return [link.tag.trim()];
      return [];
    },
    // 标签颜色映射
    getTagType(tag) {
      const t = (tag || '').toString().trim();
      if (['开源', '开源版', 'Open Source', 'OSS'].includes(t)) return 'success';
      if (['免费', 'Free'].includes(t)) return 'info';
      if (['收费', '付费', 'Pro', '订阅'].includes(t)) return 'warning';
      if (['内测', 'Beta'].includes(t)) return 'danger';
      return '';
    },

    // 获取网站favicon图标URL
    getFaviconUrl(url) {
      try {
        const domain = new URL(url).hostname;
        return `https://www.google.com/s2/favicons?domain=${domain}&sz=32`;
      } catch {
        return '/default-favicon.ico';
      }
    },
   
    // 跳转到指定分类
    scrollToCategory(categoryName) {
      if (!categoryName) return;
      
      // 查找分类在过滤后列表中的索引
      const categoryIndex = this.filteredCategories.findIndex(
        category => category.name === categoryName
      );
      
      if (categoryIndex === -1) return;
      
      // 计算目标分类所在的页码
      const targetPage = Math.floor(categoryIndex / this.pageSize) + 1;
      
      // 如果需要切换页面
      if (this.currentPage !== targetPage) {
        this.currentPage = targetPage;
      }
      
      // 等待 DOM 更新后滚动到目标分类
      this.$nextTick(() => {
        const element = document.querySelector(`[data-category="${categoryName}"]`);
        if (element) {
          element.scrollIntoView({ behavior: 'smooth' });
        }
      });
    },
    /*搜索功能*/
    performSearch() {
      const query = this.localSearchQuery.trim();
      if (!query) return;
  
      let url = '';
      switch (this.searchMode) {
        case 'Google':
          url = `https://www.google.com/search?q=${encodeURIComponent(query)}`;
          break;
        case 'Bing':
          url = `https://www.bing.com/search?q=${encodeURIComponent(query)}`;
          break;
        case 'Baidu':
          url = `https://www.baidu.com/s?wd=${encodeURIComponent(query)}`;
          break;
        case 'Sogou':
          url = `https://www.sogou.com/web?query=${encodeURIComponent(query)}`;
          break;
        default:
          url = `https://www.google.com/search?q=${encodeURIComponent(query)}`;
      }
      window.open(url, '_blank');
    },
    updateSuggestions() {
      const query = this.localSearchQuery.trim().toLowerCase();
      if (!query) {
        this.filteredSuggestions = [];
        return;
      }

      // 根据输入的内容过滤建议
      this.filteredSuggestions = this.categories
        .flatMap(category => category.links) // 获取所有链接
        .filter(link => link.name.toLowerCase().includes(query)) // 过滤匹配的链接
        .slice(0, 5); // 限制建议数量
    },
    selectSuggestion(suggestion) {
      this.localSearchQuery = suggestion.name; // 更新输入框内容
      this.performSearch(); // 执行搜索
    }
  },
  watch: {
    // 监听目标分类的变化
    targetCategory: {
      handler(newValue) {
        this.scrollToCategory(newValue);
      },
      immediate: true
    }
  }
}
</script>

