<template>
  <div>
    <!-- 移动端菜单切换按钮 -->
    <div class="mobile-toggle" :class="{ 'mobile-toggle-dark': isDarkTheme }" @click="toggleMenu">
      <el-icon size="24">
        <Expand v-if="isMobile ? !isMobileMenuVisible : isCollapsed" />
        <Fold v-else />
      </el-icon>
    </div>
    

    <div class="side-navigation" 
      :class="{
        'side-navigation-dark': isDarkTheme,
        'mobile-visible': isMobileMenuVisible,
        'collapsed': !isMobile && isCollapsed
      }">
    <!-- Logo区域 -->
     

      <!-- 桌面端显示LOGO -->
      <div class="side-nav-header" v-if="!isMobile">
      </div>
   
      <div class="logo-wrapper">
        <canvas id="logo-particles"></canvas>
        <img 
          :src="isDarkTheme ? require('../assets/logo44.png') : require('../assets/logo33.png')" 
          alt="Logo" 
          class="logo" 
        />
      </div>


          <span class="logo-text"></span>
 
        <el-button class="collapse-btn" @click="toggleCollapse">
      <el-icon>
        <Fold v-if="!isCollapsed" />
        <Expand v-else />
      </el-icon>


    </el-button>
      <div class="side-nav-content">
        <div class="unified-control-panel">
          <el-button 
            class="scroll-bottom-btn"
            @click="scrollToBottom"
          >
            <el-icon><ArrowDownBold /></el-icon>
            <!--返回底部-->
          </el-button>
          <el-input
            v-model="searchText"
            placeholder="搜索分类..."
            clearable
            prefix-icon="Search"
            @input="filterCategories"
          />
          <div :class="['statistics-info', { collapsed: isCollapsed }]"> <!-- 添加条件类 -->
            <span>当前显示: {{ filteredCategories.length }} 个分类</span>
            <span>共 {{ totalLinksCount }} 个链接</span>
          </div>
        </div>
        <el-menu
          :default-active="activeIndex"
          class="side-nav-menu"
          ref="sideMenu"
          @select="handleSelect"
        >
          <div class="menu-grid">
            <el-menu-item 
              v-for="category in filteredCategories" 
              :key="category.name" 
              :index="category.name"
              :class="{ 'is-active': activeIndex === category.name }"
            >
              <span v-if="category.emoji" class="emoji-icon">{{ category.emoji }}</span>
              <el-icon v-else>
                <component :is="category.icon || 'Folder'" />
              </el-icon>
              <span v-if="!isCollapsed" class="category-name">{{ category.name }}</span>
              <span class="item-count">{{ category.links.length }}</span>
            </el-menu-item>
          </div>
        </el-menu>
      </div>
      <div class="back-to-top-wrapper">
        <el-button 
          class="back-to-top-btn"
          @click="scrollToTop"
        >
          <el-icon><ArrowUpBold /></el-icon>
          <!--返回顶部-->
        </el-button>
      </div>
    </div>
  </div>
</template>

<script>
import { Folder, Expand, Fold, Monitor, Search, ArrowUpBold, ArrowDownBold } from '@element-plus/icons-vue'
import { navData } from "../nav-data.js"
import * as ElementPlusIcons from '@element-plus/icons-vue'
import { nextTick } from 'vue'
import { getPinyinInitials } from "../utils/pinyin.js"

export default {
  name: 'SideNavigation',
  components: {
    Folder,
    Expand,
    Fold,
    Monitor,
    Search,
    ArrowUpBold,
    ArrowDownBold,
    ...ElementPlusIcons
  },
  props: {
    isDarkTheme: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      activeIndex: '',
      categories: navData,
      isMobileMenuVisible: false,
      searchText: '',
      filteredCategories: navData,
      isMobile: window.innerWidth <= 768,
      isCollapsed: false,  // 桌面端折叠状态
    }
  },
  computed: {
    totalLinksCount() {
      return this.categories.reduce((total, category) => total + category.links.length, 0);
    }
  },
  created() {
  },
  beforeUnmount() {
    // 清理事件监听
    if (this.handleResize) {
      window.removeEventListener('resize', this.handleResize);
    }
    // 清理定时器
    if (this.timer) {
      clearInterval(this.timer);
    }
  },
  methods: {
    /**
     * 根据搜索框内容过滤分类
     * 如果搜索框为空，则显示所有分类
     * 否则根据分类名称或分类名称的拼音首字母进行匹配过滤
     */
    filterCategories() {
      // 如果搜索框为空，将过滤后的分类重置为全部分类
      if (!this.searchText) {
        this.filteredCategories = this.categories;
        return;
      }
      // 将搜索内容转换为小写，实现不区分大小写的搜索
      const searchLower = this.searchText.toLowerCase();
      // 过滤分类，只要分类名称或拼音首字母包含搜索内容，就保留该分类
      this.filteredCategories = this.categories.filter(category => {
        // 将分类名称转换为小写
        const categoryName = category.name.toLowerCase();
        // 获取分类名称的拼音首字母并转换为小写
        const pinyinInitials = getPinyinInitials(category.name).toLowerCase();
        // 判断分类名称或拼音首字母是否包含搜索内容
        return categoryName.includes(searchLower) || pinyinInitials.includes(searchLower);
      });
    },
    handleSelect(index) {
      this.activeIndex = index;
      // 发送选中的分类名称
      this.$emit('category-selected', index);
      // 发送清空顶部搜索框的事件
      this.$emit('clear-top-search');
      // 在移动端时，选择后自动隐藏菜单
      if (this.isMobile) {
        this.isMobileMenuVisible = false;


        
      }
    },
    toggleMenu() {
      if (this.isMobile) {
      // 📱 移动端：控制整块菜单的显示隐藏
      this.isMobileMenuVisible = !this.isMobileMenuVisible;
      nextTick(() => {
        window.dispatchEvent(new Event('resize'));
      });
    } else {
      // 🖥️ 桌面端：控制侧边栏宽度折叠
      this.isCollapsed = !this.isCollapsed;
    }
    },

    scrollToTop() {
      const sideContent = document.querySelector('.side-nav-content');
      if (sideContent) {
        sideContent.scrollTo({
          top: 0,
          behavior: 'smooth'
        });
      }
    },
    scrollToBottom() {
      const sideContent = document.querySelector('.side-nav-content');
      if (sideContent) {
        sideContent.scrollTo({
          top: sideContent.scrollHeight,
          behavior: 'smooth'
        });
      }
    },

      toggleCollapse() {
      this.isCollapsed = !this.isCollapsed
    }


  },
  mounted() {
      // 创建一个防抖函数
      const debounce = (fn, delay) => {
        let timer = null;
        return function (...args) {
          if (timer) clearTimeout(timer);
          timer = setTimeout(() => fn.apply(this, args), delay);
        };
      };
      // === 粒子动画初始化 ===
    const canvas = document.getElementById('logo-particles');
    const ctx = canvas.getContext('2d');
    const wrapper = document.querySelector('.logo-wrapper');

    // 匹配 LOGO 容器大小
    const resize = () => {
      canvas.width = wrapper.offsetWidth;
      canvas.height = wrapper.offsetHeight;
    };
    window.addEventListener('resize', resize);
    resize();

    // 创建粒子数组
    const particles = Array.from({ length: 25 }, () => ({
      x: Math.random() * canvas.width,
      y: Math.random() * canvas.height,
      vx: (Math.random() - 0.5) * 0.4,
      vy: (Math.random() - 0.5) * 0.4,
      size: Math.random() * 1.5 + 0.5,
      color: 'rgba(64,158,255,1)',  // Element Plus 蓝
      glow: Math.random() * 15 + 5,
    }));

    // 动画循环
    const draw = () => {
      ctx.clearRect(0, 0, canvas.width, canvas.height);

      for (const p of particles) {
        p.x += p.vx;
        p.y += p.vy;

        // 边界反弹
        if (p.x < 0 || p.x > canvas.width) p.vx *= -1;
        if (p.y < 0 || p.y > canvas.height) p.vy *= -1;

        // 发光粒子
        ctx.beginPath();
        ctx.arc(p.x, p.y, p.size, 0, Math.PI * 2);
        ctx.fillStyle = p.color;
        ctx.shadowBlur = p.glow;
        ctx.shadowColor = p.color;
        ctx.fill();
      }

      requestAnimationFrame(draw);
    };
    draw();
    // 处理 resize 事件
    const handleResize = debounce(() => {
      if (window.innerWidth > 768 && !this.isMobileMenuVisible) {
        this.isMobileMenuVisible = false;
      }
      this.isMobile = window.innerWidth <= 768
    }, 100);

    window.addEventListener('resize', handleResize);

    // 保存引用以便清理
    this.handleResize = handleResize;
  },

}
</script>

